          "html": true,
          "text": "The second son of Robert and Elizabeth Everett, Robert Everett Jr. (1822-1856) was a daguerreotypist in Utica, NY, and also worked on Y Cenhadwr Americanaidd. He was a committed abolitionist and lectured on that topic as well as temperance. Included here are temperance speeches composed in 1845. <a href=\"https://mms.newberry.org/xml/xml_files/Everett.xml#sum\" target=\"_blank\" rel=\"noreferrer\">View collection guide</a>",
