          "text": "Dating from the late 18th-century, the Everett family correspondence concerns family news and health issues, and also covers abolition, temperance, women's rights, rights of African-Americans, and moral reform. Printing, education, pioneer life, and religion are all discussed within the letters. Included are the papers of Robert Everett, the pastor of Welsh Congregationalist churches in Oneida County, New York, and publisher of Y Cenhadwr Americanaidd (The American Missionary), a Welsh religious reform magazine that was pro-abolition. Also included are letters and materials of Mary Everett, a graduate of New York Medical College and Hospital for Women, who was also involved in the suffrage movement, John Roberts and Sarah Colgrove Everett, pioneers who moved to Kansas Territory in 1854 and were active in abolitionist activities, and Cynthia Everett, a member of the American Missionary Association who taught freedmen in Norfolk, Virginia, and Charleston, South Carolina, following the Civil War. <a href=\"https://mms.newberry.org/xml/xml_files/Everett.xml#series2\" target=\"_blank\" rel=\"noreferrer\">View collection guide</a><br /><br />Cynthia Everett (1839-1876) initially taught school in and around Remsen. In 1870 she went to Norfolk, VA and then Charleston, SC, with the American Missionary Association to teach freedmen. She remained there only a short time before becoming ill and returning to Remsen, but her letters home are filled with descriptions and anecdotes about her students, coworkers, and other details about the experience. Her strong anti-slavery stance is apparent. Many of Cynthia's students and colleagues wrote to her after she left the south; these letters are included in the incoming correspondence.",
