          "html": true,
          "text": "Dating from the late 18th-century, the Everett family correspondence concerns family news and health issues, and also covers abolition, temperance, women's rights, rights of African-Americans, and moral reform. Printing, education, pioneer life, and religion are all discussed within the letters. Included are the papers of Robert Everett, the pastor of Welsh Congregationalist churches in Oneida County, New York, and publisher of Y Cenhadwr Americanaidd (The American Missionary), a Welsh religious reform magazine that was pro-abolition. Also included are letters and materials of Mary Everett, a graduate of New York Medical College and Hospital for Women, who was also involved in the suffrage movement, John Roberts and Sarah Colgrove Everett, pioneers who moved to Kansas Territory in 1854 and were active in abolitionist activities, and Cynthia Everett, a member of the American Missionary Association who taught freedmen in Norfolk, Virginia, and Charleston, South Carolina, following the Civil War. <a href=\"https://mms.newberry.org/xml/xml_files/Everett.xml#sum\" target=\"_blank\" rel=\"noreferrer\">View collection guide </a><br /><br />Letters to family from John Roberts Everett (1820-1896) and his wife, Sarah Colgrove Everett (1830-1864). John and Sarah emigrated to Kansas Territory in 1854, and the letters detail the hardships of pioneer life as well as the violent and chaotic environment created by anti-slavery and pro-slavery forces fighting over the territory's status. An abolitionist, John Roberts Everett was active in the the Free Soil movement during his time in Kansas, and present with John Brown at the Battle of Osawatomie on August 30, 1856. Due to Sarah's failing health, the family moved back east. After Sarah's death in 1864, John lived briefly with family in New York and worked for his father's printing press, but later returned to Kansas with and established himself as a farmer. Later letters describe personal news, health, and farm issues.",
