          "text": "Born in Massachusetts in 1824, Nathan Kendall served as principal in academies in Kentucky and New England before becoming a civil engineer in 1853 and moving to LaPorte, Indiana to work for the Cincinnati, Peru and Chicago Railway and then to own and operate a lumber mill. Also born in Massachusetts in 1827, Abby Reed became a New England school teacher. Friendships among the members of the Kendall and Reed families led to the marriage of Nathan and Abby in 1857. By the end of the 1860s, the couple had acquired considerable agricultural land in Illinois and Indiana, so after losing his position as a superintendent for the Cincinnati, Peru and Chicago Railway and as a mill owner, Nathan hoped to find success as a farmer. Abby bore four sons: Edgar, Winthrop, George and Arthur, losing two other children in infancy. Farming proved to be a struggle, for Nathan's health was always poor and his lungs weak, and Abby felt the need for periodic trips back to Warwick, Mass. to visit her family - especially to see her sister Mary, who was her closest friend. Nathan Kendall died in 1871, just after he had decided that perhaps relocating in the South would help his lung disease. For the next few years Abby and the children lived in New England, renting out the Indiana farm, before returning to live permanently in the Middle West. At the time of her death in 1912, Abby owned property in Fowler, Indiana and in Hudson, Massachusetts. <a href=\"https://mms.newberry.org/xml/xml_files/KendallReed.xml\" target=\"_blank\" rel=\"noreferrer\">View collection guide</a>",
