          "text": "<span>Correspondence between Christopher Gardner Pearce, who was a Mississippi steamboat captain, and his wife Jane Ann Sackett Pearce, beginning during their courtship in 1839 and continuing until 1851, the year in which Jane Ann died. The letters discuss a variety of subjects, including Pearce's life on the river, Jane Ann’s domestic life in Cincinnati, business, religion, politics, and family news and gossip. </span><a href=\"https://mms.newberry.org/xml/xml_files/Pearce.xml\" target=\"_blank\" rel=\"noreferrer\">View collection guide</a> | <a href=\"https://archive.org/details/mms_pearce\" target=\"_blank\" rel=\"noreferrer\">View in digital collection</a>",
