          "text": "Edward W. Curtis served as a private in the 88th Infantry in Nashville, Murfreesboro, and Salem in Tennessee, and Bridgeport in Alabama during the Civil War. These letters, composed between 1855 and 1865, were sent to Curtis' cousin and aunt. They discuss the Battle of Murfreesboro and describe a plantation, life as a soldier and camp conditions, and the occupied South. <a href=\"https://mms.newberry.org/xml/xml_files/Curtis.xml\" target=\"_blank\" rel=\"noreferrer\">View catalog guide</a>",
