          "text": "<br />Henry Rice served in the 103rd Illinois Infantry during the Civil War. This collection is comprised of six letters sent by Rice from Camp Peoria in Peoria, Ill. to family in Lewistown, Ill. during the autumn of 1862. They describe military life in the camp. Included with the letters is an envelope addressed to \"Mrs. Amory Rice, Lewistown, Fulton County, Illinois,\" illustrated in red and blue with the seal and motto of the United States. <a href=\"https://mms.newberry.org/xml/xml_files/Rice.xml\" target=\"_blank\" rel=\"noreferrer\">View catalog guide</a>",
