          "html": true,
          "text": "Born in 1834, Richard Realf served in the Illinois Eighty-eighth Volunteer Infantry during the Civil War. This collection includes letters composed by Realf in 1864 and 1865 and sent to Laura B. Merritt and her sister Marian Merritt Cramer of Chicago. Also included are two poems of Realf's and a poem written by Marian Cramer. <a href=\"https://mms.newberry.org/xml/xml_files/Realf.xml\" target=\"_blank\" rel=\"noreferrer\">View catalog guide</a>",
