          "text": "(pt 2) Manuscript, ca. 1914, concerning the history of the 1872-73 Modoc War, written from the Indian perspective. Also includes a history of Modoc-white contact beginning in 1848. Individuals prominently featured in the narrative include Captain Jack, John Schonchin, Alfred B. Meacham, Rev. Eleazer Thomas, Gen. E.R.S. Canby, Frank and Winema Riddle, Bogus Charley, Boston Charley, Black Jim, Hooker Jim, and Scarface Charley. <a href=\"https://webvoyage.carli.illinois.edu/nby/cgi-bin/Pwebrecon.cgi?DB=local&amp;v1=1&amp;BBRecID=146049\">View catalog record</a>",
