          "html": true,
          "text": "Papers of volunteer military officer during the Civil War, organizer of the U.S. Army Tenth Cavalry in 1866, commander of various Western army posts and active in opening the Western frontier until retirement in 1890. Includes correspondence to his wife, Alice Kirk Grierson, documents such as orders, reports and maps and other military material, as well as personal miscellany relating to his service in the Western Territories. <a href=\"https://mms.newberry.org/xml/xml_files/Grierson.xml\">View collection guide</a>",
