          "text": "Kit Carson, handicapped by illiteracy, dictated his brief autobiography to John Mostin in 1856. In the concluding paragraph of the manuscript, Carson states \"the foregoing I hereby transfer to Mr. Jesse B. Turley to be used as he may deem proper for our joint benefit.\" <a href=\"https://webvoyage.carli.illinois.edu/nby/cgi-bin/Pwebrecon.cgi?DB=local&amp;v1=1&amp;BBRecID=136955\">View catalog record</a>",
