          "html": true,
          "text": "Holograph diary of Lieut. Daniel Clapp of the 1st New Hampshire Regiment, describing his march, June 9-September 16, 1779, with Gen. Sullivan against the Six Nations. Clapp’s diary begins with the cutting of a road through the wilderness between Easton and Wyoming, and ends with news of Gen. Sullivan’s announcement that the successful campaign was over. <a href=\"https://webvoyage.carli.illinois.edu/nby/cgi-bin/Pwebrecon.cgi?DB=local&amp;v1=1&amp;BBRecID=136398\">View catalog record</a>",
